.online_content{
      position: absolute;
      top: -9rem;
      right: 2rem;
      align-items: center;
      gap: 1rem;
}

.online_avatar{
      width: 3.6rem;
      height: 3.6rem;
      position: relative;
      box-shadow: 0 0 0 .3rem rgba(0,0,0,.4);
      border-radius: 50%;
}

.online_avatar__img{
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
      overflow: hidden;
}

.online_dot{
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      box-shadow: 0 0 0 .3rem rgba(0,0,0,.4);
      position: absolute;
      bottom: 0;
      right: 0;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}

.online_content span{
      text-transform: uppercase;
      font-family: var(--ft-3);
      padding: 0 1.5rem;
      background: rgba(0,0,0,.6);
      border-radius: 999px;
      font-size: 1.2rem;
      height: 2.6rem;
      line-height: 2.4rem;
}