@import url('https://fonts.googleapis.com/css2?family=Mali:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600;1,700&family=MuseoModerno:ital,wght@0,200..900;1,100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Tilt+Warp&display=swap');

:root{
    /** fonts */
    --ft-1: 'Tilt Warp', sans-serif;
    --ft-2: 'MuseoModerno', sans-serif;
    --ft-3: 'Mali', sans-serif;
    --ft-4: 'Poppins', sans-serif;
    --ft-5: 'Roboto', sans-serif;


    /** theme */
    --primary: #c41220;
    /*--secundary: #24d1e1;*/
    --secundary: #fd4e5e;
    --v-gradient: linear-gradient(90deg, transparent, #ffffff, transparent);
    --h-gradient: linear-gradient(transparent, #ffffff, transparent);

    /*** nack **/
    --primary: #ffd700;
    /*--secundary: #bb00ff;
    */
    
    /** colors */
    --bg-400: #181d23;
    --bg-300: #20262d;
    --bg-200: #fafafa;
    --bg-100: #ffffff;
    --text-400: #181d23;
    --text-300: #cccccc;
    --text-200: #e3e3e3;
    --text-100: #fff;
    --border: #373b4c;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #171717;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-3);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-400);
    color: var(--text-100);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-3);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    widtH: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 114rem;
    height: auto;
    position: relative;
    gap: 2.5rem;
    margin: 0 auto;
}

@property --a{
    syntax: '<angle>';
    inherits: false;
    initial-value: 0deg;
}

.effect{
    position: relative;
    width: fit-content;
    height: fit-content;
    overflow: hidden;
}

.effect:before{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: repeating-conic-gradient(from var(--a), var(--primary) 0%, var(--primary) 5%, transparent 5%, transparent 40%, var(--primary) 50%);
    animation: rotating 4s linear infinite;
}

.effect:after{
    background: var(--bg-200);
    top: .2rem;
    left: .2rem;
}

@keyframes rotating{
    0%{
        --a: 0deg;
    }
    100%{
        --a: 360deg;
    }
}

.scroll-y{
    width: 100%;
    height: 42.5rem;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-200), var(--primary);
}

.scroll-x{
    width: calc(100% - 13.6rem);
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-200), var(--primary);
}

.scroll-y::-webkit-scrollbar,
.scroll-x::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-200);
    border-radius: .8rem;
}

.scroll-y::-webkit-scrollbar-thumb,
.scroll-x::-webkit-scrollbar-thumb{
    background: var(--primary);
    border-radius: .8rem;
}

::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-200);
    border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
    background: var(--primary);
    border-radius: .8rem;
}