.icon_page{
      width: 7.6rem;
      height: 7.6rem;
      position: absolute;
      left: calc(50% - 3.8rem);
      top: calc(100% - 3.8rem);
      z-index: 4;
      align-items: center;
      justify-content: center;
      background: var(--bg-100);
      border-radius: 50%;
      border: solid .3rem var(--primary);
      opacity: 0;
      transform: translateY(-50%);
      transition: .5s ease-in-out;
}

.icon_page *{
      color: var(--bg-400);
      font-size: 4rem;
}

.icon_page.active{
      opacity: 1;
      transform: translateY(0);
}

@keyframes bounce{
      0%{
            opacity: 0;
            transform: translateY(-50%);
      }
      75%{
            opacity: 1;
            transform: translateY(50%);
      }
      100%{
            opacity: 1;
            transform: translateY(0);
      }
}