.modal_content{
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      z-index: 100;
      align-items: center;
      justify-content: center;
      background: rgba(0,0,0,.7);
      opacity: 0;
      visibility: hidden;
      transition: .4s ease-in-out;
}

.modal_content.active{
      opacity: 1;
      visibility: visible;
}

.inner_modal{
      width: fit-content;
      height: fit-content;
      max-width: 90%;
      max-height: 90vh;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.btn_close__modal{
      position: absolute;
      width: 4rem;
      height: 4rem;
      bottom: 0;
      right: 0;
      background: var(--primary);
      align-items: center;
      justify-content: center;
}

.btn_close__modal *{
      color: var(--light);
      font-size: 2.4rem;
}