.user_content{
      position: relative;
      width: 100%;
      height: auto;
      z-index: 2;
      padding-top: 11.6rem;
}



.user_group{
      grid-template-columns: 1fr 24rem 1fr;
      gap: 4rem;
}

.col_grid.menu{
      align-items: flex-end;
      flex-direction: column;
}

.header_menu{
      width: 15rem;
      gap: 1rem;
      align-items: center;
      position: relative;
}

.header_menu:before{
      width: 2.4rem;
      height: .2rem;
      background: var(--light);
      right: 1rem;
      top: 1.6rem;
      box-shadow: 0 .6rem var(--light);
}

.header_menu__icon{
      width: 3.6rem;
      height: 3.6rem;
      align-items: center;
      justify-content: center;
      background: var(--primary);
      box-shadow: 0 0 0 .3rem rgba(0,0,0,.6);
      border-radius: 50%;
}

.header_menu span{
      text-transform: uppercase;
      font-size: 2.2rem;
      color: var(--text-400);
}

.nav_bar{
      width: 15rem;
      margin-top: 2rem;
      padding-left: 4.5rem;
}

.nav_bar ul li{
      margin-bottom: 1rem;
}

.btn_nav__link{
      position: relative;
      height: 2.4rem;
      width: fit-content;
      background: none;
      color: var(--text-100);
}

.btn_nav__link:before,
.btn_nav__link:after{
      width: 1rem;
      height: 1rem;
      background: var(--bg-100);
      top: calc(50% - .5rem);
      opacity: 0;
      transition: .3s ease-in-out;
}

.btn_nav__link:before{
      left: -4rem;
}

.btn_nav__link:after{
      right: -4rem;
}

.btn_nav__link:hover:before,
.btn_nav__link.active:before{
      left: -2rem;
      opacity: 1;
}

.btn_nav__link:hover:after,
.btn_nav__link.active:after{
      right: -2rem;
      opacity: 1;
}

.btn_nav__link span{
      display: flex;
      font-size: 2rem;
      line-height: 2.4rem;
      position: relative;
}

.btn_nav__link span:before{
      width: 0;
      height: .2rem;
      background: var(--bg-100);
      bottom: 0;
      left: 0;
}

.btn_nav__link.active span:before{
      width: 100%;
      left: 0;
}

.btn_nav__link:hover span:before{
      animation: mov .3s ease-in-out forwards;
}

@keyframes mov{
      0%{
            width: 0;
            left: 0;
      }
      50%{
            width: 50%;
            left: 50%;
      }
      100%{
            width: 0;
            left: 100%;
      }
}

.user_data{
      flex-direction: column;
      align-items: center;
}

.user_data__avatar,
.user_data__avatar_img{
      width: 12rem;
      height: 12rem;
      position: relative;
}

.user_data__avatar:before,
.user_data__avatar:after{
      border-radius: 50%;
      border: solid .2rem var(--bg-100);
}

.user_data__avatar:before{
      top: -.8rem;
      left: -.8rem;
      width: 13rem;
      height: 13rem;
}

.user_data__avatar:after{
      top: -1.3rem;
      left: -1.3rem;
      width: 14rem;
      height: 14rem;
}

.user_data__avatar_img{
      overflow: hidden;
      border-radius: 50%;
}

.user_data h1{
      margin-top: 2rem;
      font-size: 3rem;
      text-transform: uppercase;
}

.user_data strong{
      display: inline-block;
      position: relative;
      width: 13rem;
      text-align: center;
      font-size: 2.4rem;
      color: var(--text-400);
}

.user_data strong:before{
      width: 13rem;
      height: 1.8rem;
      border: solid .2rem var(--bg-100);
      border-top: solid .2rem transparent;
      left: 0;
      top: 35%;
}

.user_data p{
      margin-top: 1rem;
      text-align: center;
}

.user_infor{
      margin-top: 2rem;
}

.user_network{
      align-items: center;
      gap: 1rem;
}

.user_network__link{
      width: 8rem;
      height: 2.6rem;
      justify-content: center;
      background: var(--bg-400);
      color: var(--text-100);
      border-radius: 999px;
      line-height: 2.4rem;
}

.user_social{
      margin-top: 2rem;
      align-items: center;
      gap: 1rem;
}

.user_location{
      margin-top: 2rem;
      align-items: center;
      gap: 1rem;
}

.user_location *{
      font-size: 2.6rem;
}

.user_location span{
      font-size: 2rem;
}