.friends_content{
      width: 100%;
      height: 13.6rem;
      padding: 1.5rem;
      position: absolute;
      background: var(--bg-300);
      z-index: 10;
      left: 0;
      bottom: 0;
      transform: translateY(100%);
      transition: .4s ease-in-out;
}

.friends_content.active{
      transform: rotateY(0);
}

.friends_group{
      width: calc(100% - 13.6rem);
      height: 100%;
      padding: .5rem;
      background: var(--bg-400);
      border-radius: 1rem;
      overflow-x: auto;
      overflow-y: initial;
}

.friends_list{
      width: fit-content;
      height: 8rem;
      align-items: center;
      gap: 2rem;
}

.friend_row{
      width: 25rem;
      height: 100%;
      border-radius: .6rem;
      background: var(--bg-300);
      gap: .5rem;
}

.friend_row__avatar{
      width: 8rem;
      height: 8rem;
      border-radius: .6rem 0 0 .6rem;
      overflow: hidden;
}

.friend_row__info{
      flex: 1;
      padding: .5rem;
      position: relative;
}

.friend_row__info:before{
      width: .1rem;
      height: 6rem;
      background: #fff;
      top: 1rem;
      left: 0;
      opacity: 0.17;
}

.friend_row__info > span{
      color: var(--primary);
}

.friend_row__info p{
      font-size: 1.4rem;
}

.btn_friend{
      height: 2.4rem;
      gap: 1rem;
      margin-top: .5rem;
      align-items: center;
}

.btn_friend span{
      display: block;
      width: 8rem;
      height: 2.2rem;
      text-align: center;
      line-height: 1.8rem;
      background: var(--bg-400);
      border-radius: 999px;
      color: var(--primary);
}

.icon_btn_friend{
      width: 2rem;
      height: 2rem;
      align-items: center;
      justify-content: center;
      background: var(--bg-400);
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--bg-100);
}

.icon_btn_friend *{
      color: var(--bg-100);
      font-size: 1.2rem;
}