.wrapper_image{
      width: 100%;
      height: fit-content;
      position: relative;
      border-radius: 1rem;
      overflow: hidden;
      margin-bottom: 2rem;
}

.wrapper_image:before{
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.6);
      top: 0;
      left: 0;
      opacity: 0;
      transition: .3s ease-in-out;
}

.wrapper_image:hover:before{
      opacity: 1;
}

.image_box{
      width: 100%;
      height: 100%;
      overflow: hidden;
}

.image_box img{
      transform: scale(1.2);
      transition: .2s ease-in-out;
      filter: grayscale(0);
}

.wrapper_image:hover .image_box img{
      transform: scale(1);
      filter: grayscale(1);
}

.btn_image{
      width: 4rem;
      height: 4rem;
      position: absolute;
      top: 2rem;
      right: 2rem;
      background: rgba(255,255,255,.3);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      z-index: 2;
}

.btn_image *{
      font-size: 2.6rem;
      color: var(--text-100);
}