.header_pager{
      width: 100%;
      height: auto;
      align-items: center;
      justify-content: space-between;
}

.header_pager__user{
      height: 4.2rem;
      gap: 1rem;
}

.header_pager__user_img{
      width: 3.6rem;
      height: 3.6rem;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 0 .3rem var(--bg-100), 0 0 0 .6rem var(--primary);
      margin: .3rem;
}

.user_name__flag{
      align-items: center;
      gap: .5rem;
}

.user_name__flag h3{
      color: var(--primary);
      font-family: var(--ft-1);
      text-transform: uppercase;
      font-size: 2rem;
      font-weight: 500;
}

.user_name__flag img{
      width: 2.6rem;
}

.header_pager__user_info span{
      color: var(--text-400);
}

.header_pager__dots{
      width: .8rem;
      height: .8rem;
      background: var(--text-200);
      box-shadow: -1.4rem 0 var(--text-200), -2.8rem 0 var(--text-200);
}