.banner_content{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
}

.banner_shaper{
      width: 105%;
      height: 100%;
      border-radius: 0% 100% 50% 50% / 0% 0% 100% 100%;
      overflow: hidden;
      background: var(--primary);
      position: relative;
      left: -2.5%;
}

.banner_shaper:before{
      width: 100%;
      height: 100%;
      overflow: hidden;
      background: linear-gradient(var(--primary), rgba(196, 18, 32, .65));
      left: 0;
      top: 0;
      z-index: 1;
}

.banner_shaper img{
      width: 100%;
      height: 100%;
      border-radius: 0% 100% 50% 50% / 0% 0% 100% 100%;
      overflow: hidden;
      filter: grayscale(1) contrast(130%);
      mix-blend-mode: multiply;
      opacity: 1;
}