.main_content{
      width: 100%;
      height: 100vh;
      min-height: 92rem;
      position: relative;
      overflow: hidden;
}

.content{
      width: 100%;
      height: 47.8rem;
      margin-top: 6rem;
      position: relative;
      justify-content: center;
}

.content:before,
.content:after{
      width: 15rem;
      height: 100%;
      background: var(--bg-100);
      top: 0;
}

.content:before{
      left: 0;
      border-radius: 0 3rem 3rem 0;
}

.content:after{
      right: 0;
      border-radius: 3rem  0 0 3rem;
}

.wrapper_pages,
.pages_group,
.pager{
      width: 66.2rem;
      height: 47.8rem;
      position: relative;
}

.pager{
      position: absolute;
      background: var(--bg-100);
      padding: 2.5rem;
      border-radius: 2rem;
      opacity: 0;
      visibility: hidden;
      top: 0;
      left: 0;
      transform: translateX(-30%);
      transition: .5s ease-in-out;
}

.pager.active{
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
}

.pager_inner{
      width: 59.4rem;
      height: 38.4rem;
      background: var(--bg-200);
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      border-radius: 2.5rem 2.5rem 0 0;
      padding: 2.5rem;
}

.pager_inner__content{
      width: 100%;
      height: 100%;
      padding-right: 1rem;
      color: var(--text-400);
      overflow-y: auto;
}

.data_info__col{
      align-items: center;
      justify-content: center;
      height: 22rem;
}

.data_info__col:nth-child(1){
      border-right: solid .1rem #dedede;
}

.data_info__avatar{
      width: 21.6rem;
      height: 19rem;
      border-radius: 1rem;
      overflow: hidden;
}

.data_info__col ul li{
      display: flex;
      align-items: center;
      font-size: 1.8rem;
}

.data_info__col ul li strong{
      padding-right: 1rem;
      color: var(--text-300);
      text-transform: uppercase;
}

.data_info__col ul li a{
      padding: 0 1rem;
      background: var(--primary);
      border-radius: 999px;
      color: var(--text-100);
      line-height: 1.8rem;
      height: 1.4em;
      font-size: 1.4rem;
      margin-left: .5rem;
}

.data_info__about{
      margin-top: 4rem;
}

.data_info__about p{
      white-space: pre-line;
}

.wrapper_btn_friends{
      width: 12.6rem;
      height: 13.6rem;
      flex-direction: column;
      align-items: center;
      position: fixed;
      bottom: 0;
      right: 0;
      z-index: 12;
      justify-content: center;
}

.wrapper_btn_friends > span{
      height: 2.2rem;
      background: var(--bg-400);
      padding: 0 1rem;
      border-radius: 999px;
      line-height: 2rem;
      font-size: 1.2rem;
      color: var(--primary);
}

.btn_toggle__friend{
      position: relative;
      width: 4rem;
      height: 4rem;
      background: var(--primary);
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      box-shadow: 0 0 0 .5rem var(--bg-400);
      margin-top: 1rem;
}

.icon_friend{
      width: 4rem;
      height: 4rem;
      align-items: center;
      justify-content: center;
}

.icon_friend *{
      color: var(--text-100);
      font-size: 2rem;
}

.btn_toggle__friend span{
      width: 2.4rem;
      height: 2.4rem;
      position: absolute;
      background: var(--bg-100);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      border: solid .3rem var(--bg-400);
      line-height: 1.8rem;
      bottom: -.5rem;
      right: -.75rem;
      font-size: 1.2rem;
}